.box {
  display: inline-block;
  font-size: 100%;
  margin: 30px 90px;
  min-height: 700px;
  text-align: left;
  width: 30%;
  word-warp: break-word;
  border-radius: 10px;
}
.left {
  background-color: #A9A9A9;
  float: left;
  padding-top: 15px;
}
.right {
  background-color: #FF7F50;
  float: right;
  padding-top: 15px;
  border: 1px solid #222;
}
@media (max-width: 992px) {
  .box {
    margin: 10px 10px;
  }
}
@media (max-width: 768px) {
  .box {
    margin: 10px 10px;
  }
}
@media (max-width: 576px) {
  .box {
    margin: 10px 10px;
  }
}
textarea {
  border: none;
  resize: none;
}
footer {
  position: fixed;
  bottom: 0%;
  padding-bottom: 0.2em;
  width: 100%;
  background-color: #282c34;
  color: #F8F8F8;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
